import {
  AfterContentInit,
  ChangeDetectionStrategy,
  ChangeDetectorRef,
  Component,
  ContentChildren,
  ElementRef,
  EventEmitter,
  forwardRef,
  Input,
  OnChanges,
  OnDestroy,
  OnInit,
  Optional,
  Output,
  QueryList,
  SimpleChanges,
  ViewEncapsulation
} from '@angular/core';
import { ControlValueAccessor, NG_VALUE_ACCESSOR } from '@angular/forms';
import { NavigationEnd, Router, RouterLink, RouterLinkWithHref } from '@angular/router';
import { Subject } from 'rxjs';
import { delay, filter, startWith, takeUntil } from 'rxjs/operators';
import { InputBoolean, OnChangeType, OnTouchedType, TDSSafeAny } from 'tds-ui/shared/utility';
import { TDSFilterStatusItemComponent } from './filter-status-item.component';
import { TDSFilterStatusLinkComponent } from './filter-status-link.component';
import { TDSFilterStatusService } from './filter-status.service';

@Component({
  selector: 'tds-filter-status',
  exportAs: 'tdsFilterStatus',
  preserveWhitespaces: false,
  encapsulation: ViewEncapsulation.None,
  changeDetection: ChangeDetectionStrategy.OnPush,
  template: `
  <ng-content></ng-content>
  `,
  host: {
    class: 'inline-block',
  },
  providers: [
    TDSFilterStatusService,
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => TDSFilterStatusComponent),
      multi: true
    }
  ]
})
export class TDSFilterStatusComponent implements OnInit, OnDestroy, AfterContentInit, OnChanges, ControlValueAccessor {
  private _value: TDSSafeAny | null = null;
  get value() {
    return this._value;
  }
  set value(v: TDSSafeAny) {
    if (this._value !== v) {
      this._value = v;
    }
  }
  @Input() @InputBoolean() disabled = false;
  @Input() @InputBoolean() linkRouter = false;
  @Input() @InputBoolean() linkExact = true;
  @Output('selectChange') readonly selectChangeEvent = new EventEmitter<TDSSafeAny>();
  @ContentChildren(TDSFilterStatusItemComponent) listChild!: QueryList<TDSFilterStatusItemComponent>;
  @ContentChildren(TDSFilterStatusLinkComponent) listLinkChild!: QueryList<TDSFilterStatusLinkComponent>;
  links: QueryList<TDSFilterStatusLinkComponent> = new QueryList<TDSFilterStatusLinkComponent>();
  private destroy$ = new Subject<void>();
  constructor(
    private cdr: ChangeDetectorRef,
    @Optional() private tdsFilterStatusService: TDSFilterStatusService,
    @Optional() private router: Router
  ) { }



  ngOnInit(): void {
    if (this.tdsFilterStatusService) {
      this.tdsFilterStatusService.selected$.pipe(takeUntil(this.destroy$)).subscribe(value => {
        if (this.value !== value) {
          this.value = value;
          this.selectChangeEvent.emit(this.value);
          this.onChange(this.value);
        }
      });
    }
  }
  ngOnDestroy(): void {
    this.destroy$.next();
    this.destroy$.complete();
  }
  ngAfterContentInit(): void {
    Promise.resolve().then(() => {
      this.setUpRouter();
    });
  }
  ngOnChanges(changes: SimpleChanges): void {

  }
  onChange: OnChangeType = () => { };
  onTouched: OnTouchedType = () => { };
  writeValue(value: TDSSafeAny): void {
    this.value = value;
    this.tdsFilterStatusService.select(value);
    this.cdr.markForCheck();
  }

  registerOnChange(fn: OnChangeType): void {
    this.onChange = fn;
  }

  registerOnTouched(fn: OnTouchedType): void {
    this.onTouched = fn;
  }
  private setSelectedIndex(value: TDSSafeAny) {
    if (value != this.value)
      this.tdsFilterStatusService.select(value);
  }
  private setUpRouter(): void {
    if (this.linkRouter) {
      if (!this.router) {
        throw new Error(`[TDS] you should import 'RouterModule' if you want to use 'linkRouter'!`);
      }
      this.router.events
        .pipe(
          takeUntil(this.destroy$),
          filter(e => e instanceof NavigationEnd),
          startWith(true),
          delay(0)
        )
        .subscribe(() => {
          this.updateRouterActive();
          this.cdr.markForCheck();
        });
    }
  }
  private updateRouterActive(): void {
    if (this.router.navigated) {
      const index = this.findShouldActiveTabIndex();
      if (index > -1) {
        const links = this.listLinkChild.toArray();
        this.setSelectedIndex(links[index].value);
      }
    }
  }
  private findShouldActiveTabIndex(): number {
    const links = this.listLinkChild.toArray();
    const isActive = this.isLinkActive(this.router);

    return links.findIndex(link => {
      const c = link.linkDirective;
      return c ? isActive(c.routerLink) || isActive(c.routerLinkWithHref) : false;
    });

  }
  private isLinkActive(router: Router): (link?: RouterLink | RouterLinkWithHref) => boolean {
    return (link?: RouterLink | RouterLinkWithHref) => (link ? router.isActive(link.urlTree ? link.urlTree : '' , this.linkExact) : false);
  }
}
