import { ChangeDetectionStrategy, ChangeDetectorRef, Component, ContentChild, ElementRef, Input, OnChanges, OnDestroy, OnInit, Optional, SimpleChanges, ViewEncapsulation } from "@angular/core";
import { Subject } from "rxjs";
import { takeUntil } from "rxjs/operators";
import { TDSSafeAny } from "tds-ui/shared/utility";
import { TDSFilterStatusLinkDirective } from "./filter-status-link.directive";
import { TDSFilterStatusService } from "./filter-status.service";

@Component({
    selector: 'tds-filter-status-link',
    templateUrl: './filter-status-link.component.html',
    exportAs: 'tdsFilterLink',
    preserveWhitespaces: false,
    encapsulation: ViewEncapsulation.None,
    changeDetection: ChangeDetectionStrategy.OnPush,
    host: {
      '(click)': 'onHostClick($event)',
      '[class.cursor-pointer]':'!disabled', 
      '[class.cursor-not-allowed]':'disabled',
      'class': "inline-block"
    }
  })
  export class TDSFilterStatusLinkComponent implements OnInit, OnDestroy,OnChanges {
    active: boolean = false;
    @Input() disabled: boolean = false;
    @Input() value: TDSSafeAny | null = null;
    @Input() count: number | null = null;
    @Input() name: string | null = null;
    @ContentChild(TDSFilterStatusLinkDirective, { static: false }) linkDirective!: TDSFilterStatusLinkDirective;
    private destroy$ = new Subject<void>();
    constructor(private elementRef: ElementRef,
      private cdr: ChangeDetectorRef,
      @Optional() private tdsFilterStatusService: TDSFilterStatusService) { }
    ngOnInit(): void {
      if (this.tdsFilterStatusService) {
        this.tdsFilterStatusService.disabled$.pipe(takeUntil(this.destroy$)).subscribe(disabled => {
          this.disabled = disabled;
          this.cdr.markForCheck();
        });
        this.tdsFilterStatusService.selected$.pipe(takeUntil(this.destroy$)).subscribe(value => {
          
          this.active = this.value === value;
          this.cdr.markForCheck();
        });  
      }
    }
    onHostClick(event: MouseEvent) {
      /** prevent label click triggered twice. **/
      event.stopPropagation();
      event.preventDefault();
      if (!this.disabled && !this.active) {
        if (this.tdsFilterStatusService) {
          this.tdsFilterStatusService.select(this.value);
        }
      }
  
    }
    ngOnDestroy(): void {
      this.destroy$.next();
      this.destroy$.complete();
    }
    ngOnChanges(changes: SimpleChanges): void {
     this.cdr.markForCheck();
    }
  }
  