import { Directive, ElementRef, Optional, Self } from "@angular/core";
import { RouterLink, RouterLinkWithHref } from "@angular/router";

/**
 * This component is for catching `routerLink` directive.
 */
 @Directive({
    selector: 'a[tds-filter-status-router-link]',
    exportAs: 'tdsFilterStatusRouterLink'
  })
  export class TDSFilterStatusLinkDirective {
    constructor(
      public elementRef: ElementRef<HTMLAnchorElement>,
      @Optional() @Self() public routerLink?: RouterLink,
      @Optional() @Self() public routerLinkWithHref?: RouterLinkWithHref
    ) {}
  }
  